$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

@import "@/asset/sass/variable.scss";
.seach-page {
  width: 1400px;
  height: 100%;
  margin: 0 auto;
  .result {
    height: 18px;
    margin: 29px 0 22px;
    font-family: MicrosoftYaHeiLight;
    font-size: 18px;
    line-height: 18px;
    color: rgba(114, 113, 113, 1);
    span {
      color: #ef0716;
    }
  }
  .hot {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 30px;
    margin: 50px 0 13px;
    font-size: 18px;
    font-weight: 400;
    line-height: 30px;
    color: $black;

    -webkit-align-items: center;

            align-items: center;
    img {
      width: 30px;
      height: 30px;
    }
  }
  .hot-list {
    display: -webkit-flex;
    display: flex;
    width: 100%;

    -webkit-flex-wrap: wrap;

            flex-wrap: wrap;
    .hot-item {
      margin: 0 16px 16px 0;
    }
  }
}
